<template>
    <ModalLayout :hasClose="false" :hasDim="false">
        <template v-slot:header>
            <h1 class="tit">{{data.title}}</h1>
        </template>
        <template v-slot:body>
            {{data.msg}}
        </template>
        <template v-slot:footer>
            <div class="wrap-footer d-flex justify-content-end">
                <button class="btn" v-if="data.hasCallback" @click="callback">OK</button>
                <button class="btn" @click="close">Close</button>
            </div>
        </template>
    </ModalLayout>
</template>

<script>
import ModalLayout from '@/components/modals/index/ModalLayout.vue';
import { modalAlertInit } from './modalAlert';

export default {
    name: 'ModalAlert',
    components: { ModalLayout },
    props: ['currentProperty'],
    setup (props) {
        const {
            data, close, callback
        } = modalAlertInit(props.currentProperty);

        return {
            data, close, callback
        }
    }
}
</script>

<style lang="scss" scoped src="./modalAlert.scss"></style>