import { ref, reactive, computed, onMounted, watch } from 'vue';
import { useStore } from 'vuex';
import { useEmitter } from '@/utils/globalInstance';

export const notiInit = () => {
    const store = useStore();
    const emitter = useEmitter();

    const notiList = computed(() => store.state.notification.list);
    let timeout = null;

    onMounted(() => {
        timeoutNotiHandle();
    })

    watch(notiList.value, () => {
        timeoutNotiHandle();
    })

    const timeoutNotiHandle = () => {
        if(notiList.value.length > 0) {
            clearTimeout(timeout);
            timeout = setTimeout(() => {
                store.commit('removeNotification', notiList.value.length - 1)
            }, 30000);
        }
    }

    const removeNotiHandle = (index) => {
        store.commit('removeNotification', index);
    }

    return {
        notiList,
        removeNotiHandle
    }
}