<template>
	<div class="wrap-home">
		<!-- <div class="avatar">
			<img src="" alt="">
		</div> -->
		<div class="content">
			<p class="text-center" ref="textRef">Praise the clouds!</p>
		</div>
	</div>
</template>

<script>
import { onBeforeUnmount, onMounted, ref } from '@vue/runtime-core';
export default {
	name: "Home",
	setup() {
		const textRef = ref();
		let timeout = null;
		onMounted(() => {
			timeout = setTimeout(() => {
				textRef.value.classList.add('show')
			}, 10000)
		})

		onBeforeUnmount(() => {
			clearTimeout(timeout)
		})

		return {
			textRef
		}
	}
};
</script>

<style lang="scss" scoped src="./home.scss"></style>
