import { computed, onBeforeUnmount, onMounted, reactive, ref } from "vue";
import { DateDiff, getFormatedStringFromDays } from "@/utils/commonFunc";

export const init = () => {
    const mounted = ref(false);
    const tabExp = ref("main");
    const tabInterest = ref("pos");
    const tabSkill = ref("main");
    const toggleInfo = reactive({
        email: false,
        phone: false,
    })
    const yearExp = computed(() => {
        let currentDate = new Date();
        let theDateBegin = new Date("11, 01, 2019");
        let day = DateDiff.inDays(theDateBegin, currentDate);
        let month = DateDiff.inMonths(theDateBegin, currentDate);
        let year = DateDiff.inYears(theDateBegin, currentDate);
        let result = {
            float: parseFloat(month / 12).toFixed(1),
            year: Math.floor(month / 12),
            month: month % 12
        };
        return {day, month, year, result};
    })

    onMounted(() => {
        setTimeout(() => {
            mounted.value = true;
        }, 0);
    })

    const toggleEmail = () => {
        toggleInfo.email = !toggleInfo.email;
    }

    const togglePhone = () => {
        toggleInfo.phone = !toggleInfo.phone;
    }

    return {
        mounted,
        tabExp,
        tabInterest,
        tabSkill,
        toggleInfo,
        yearExp,
        toggleEmail,
        togglePhone
    }
}