const auth = {
    state: {
        auth: null,
        loader: true,
        theme: 'light',
    },
    mutations: {
        setAuth: function(state, payload) {
            state.auth = payload;
        },
        setTheme: function(state, payload) {
            state.theme = payload;
            document.documentElement.dataset.theme = payload;
        }
    },
    getters: {
        getAuth: state => state.auth,
        getTheme: state => state.theme
    },
    action: {
        resetInfoAccount({commit}, payload) {
            // do something
        }
    }
}

export default auth;