const notification = {
    state: {
        list: [],
    },
    mutations: {
        addNotification: function(state, payload) {
            state.list.push(payload);
        },
        removeNotification: function(state, index) {
            state.list.splice(index, 1);
        },
        setNotification: function(state, payload) {
            state.list = payload;
        }
    }
}

export default notification;