import { onMounted, ref, watch } from "vue";
import { useRoute } from 'vue-router';

export const init = () => {
    const routeActive = ref('');
    const route = useRoute();
    const menuActive = ref(false);

    onMounted(() => {
        setIndex();
    })
    
    watch(() => route.name, () => {
        setIndex();
    })

    const setIndex = () => {
        switch (route.name) {
            case 'Home': routeActive.value = ''; break;
            case 'Profile': routeActive.value = 'profile'; break;
            case 'Manip': routeActive.value = 'manip'; break;
            case 'Projects':
            case 'ProjectDetail': routeActive.value = 'projects'; break;
            case 'Sharing': routeActive.value = 'sharing'; break;
        }
    }

    return {
        routeActive,
        menuActive
    }
}