import { reactive } from "vue";
import { useEmitter } from '@/utils/globalInstance';

export const modalAlertInit = (props) => {
    const emitter = useEmitter();

    const data = reactive({
        title: props.params.title,
        type: props.params.type,
        msg: props.params.msg,
        hasCallback: props.callback ? true : false
    })

    const callback = () => {
        if(props.callback) props.callback();
        emitter.emit('system/closeMessageBox');
    };

    const close = () => {
        emitter.emit('system/closeMessageBox');
    }

    return {
        data, close, callback
    }
}