<template>
	<div>
        <div class="bg-inner"></div>
		<router-view></router-view>

        <component
            :is="modalLevel1.el"
            :currentProperty="modalLevel1.params"
        ></component>
        <component
            :is="modalLevel2.el"
            :currentProperty="modalLevel2.params"
        ></component>

        <component
            :is="alert.el"
            :currentProperty="alert.params"
        ></component>

        <div :class="['page-loader', {show: loader > 0}]">
            <Loader v-if="loader > 0" />
        </div>

        <Notification v-if="notiList.length > 0" />
	</div>
</template>

<script>
import "@/scss/theme.style.scss";

import {
    ref,
    reactive,
    computed,
    watch,
    onMounted,
    shallowRef,
    onBeforeUnmount,
} from "vue";
import { useStore } from "vuex";
import ModalWelcome from "@/components/modals/modalWelcome/ModalWelcome.vue";
import ModalAlert from "@/components/modals/modalAlert/ModalAlert.vue";
import { useEmitter } from "@/utils/globalInstance";
import Loader from '@/components/Loader.vue';
import Notification from '@/components/notification/Notification.vue';
import imgBg from '@/assets/images/bg_home_3.jpg';

export default {
	name: "App",
    components: {
        Loader,
        Notification
    },
    setup() {
        const store = useStore();
        const emitter = useEmitter();

        const modalLevel1 = shallowRef({ el: null, params: null });
        const modalLevel2 = shallowRef({ el: null, params: null });
        const alert = shallowRef({ el: null, params: null });
        const loader = computed(() => store.state.loader.processRunning);
        const notiList = computed(() => store.state.notification.list);

        onBeforeUnmount(() => {
            emitter.off("system/openModal");
            emitter.off("system/closeModal");
            emitter.off("system/openMessageBox");
            emitter.off("system/closeMessageBox");
        });

        onMounted(() => {
            emitter.on("system/openModal", openModalHandler);
            emitter.on("system/closeModal", closeModalHandler);
            emitter.on("system/openMessageBox", openMessageHandler);
            emitter.on("system/closeMessageBox", closeMessageHandler);

            preLoadBg();
        });

        watch([modalLevel1, modalLevel2], () => {
            // disable scrollbar
            if (modalLevel1.value.el != null || modalLevel2.value.el != null) {
                if (!document.body.classList.contains("hidden-scroll")) {
                    document.body.classList.add("hidden-scroll");
                }
            } else {
                document.body.classList.remove("hidden-scroll");
            }
        });

        const preLoadBg = () => {
            let img = new Image();
            img.src = imgBg;
            store.commit('addProcessRunning', true);
            img.onload = function() {
                setTimeout(() => {
                    store.commit('addProcessRunning', false);
                }, 500)
            }
        }

        const openModalHandler = (params) => {
            let currentComponent = {
                el: null,
                name: params.name,
                params: {
                    params: params.params,
                    callback: params.callback,
                },
            };

            switch (params.name) {
                case "modalWelcome": currentComponent.el = ModalWelcome; break;
            }

            if (currentComponent.el) {
                if (modalLevel1.value.el) modalLevel2.value = currentComponent;
                else modalLevel1.value = currentComponent;
            }
        };

        const closeModalHandler = () => {
            if (modalLevel2.value.el)
                modalLevel2.value = { el: null, params: null };
            else modalLevel1.value = { el: null, params: null };
        };

        const openMessageHandler = (params) => {
            alert.value = {
                el: ModalAlert,
                params: {
                    params: params.params, // title, msg
                    callback: params.callback,
                },
            };
        };

        const closeMessageHandler = () => {
            alert.value = { el: null, params: null };
        };

        return {
            alert,
            modalLevel1,
            modalLevel2,
            loader,
            notiList
        };
    },
};
</script>

<style lang="scss" scoped>
.bg-inner {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: url("~@/assets/images/bg_home_3.jpg") center no-repeat;
    background-attachment: fixed;
    background-size: cover;
    opacity: .5;
    height: 100vh;
}
.page-loader {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: 1s;
    &.show {
        opacity: 1;
        visibility: visible;
    }
}
</style>
