const loader = {
    state: {
        processRunning: 0
    },
    mutations: {
        addProcessRunning: (state, status) => {
            if(status) state.processRunning = state.processRunning + 1;
            else state.processRunning = state.processRunning - 1;
            // console.log('process running: ', state.processRunning);
            if(state.processRunning < 0) state.processRunning = 0; // in case of error;
        }
    },
    getters:{
        processRunning(state){
            return state.processRunning;
        }
    }
}

export default loader;
