const modal = {
    state: {
        currentModals: [], // save multi modal's name, multi modals will be displayed in multi level
    },
    getters: {
        currentModals: state => state.currentModals,
    },
    mutations: {
        setCurrentModals(state, payload) {
            state.currentModals = payload;
        },
        addCurrentModals(state, payload) {
            if(!state.currentModals.includes(payload)) {
                state.currentModals.push(payload);
            }
        },
        removeCurrentModals(state, payload) {
            let index = state.currentModals.findIndex(x => x === payload);
            if(index > -1) state.currentModals.splice(index, 1);
        },
        toggleCurrentModals(state, payload) {
            let index = state.currentModals.findIndex(x => x === payload);
            if(index > -1) state.currentModals.splice(index, 1)
            else state.currentModals.push(payload)
        }
    },
    actions: {

    }
}

export default modal;