import { computed, onMounted, reactive, watch } from "vue";
import { useRoute } from "vue-router";
import { useStore } from "vuex";

export const init = () => {
    const data = reactive({});
    const route = useRoute();
    const store = useStore();
    const loader = computed(() => store.state.loader.processRunning);

    watch(() => route.name, () => {
        window.scrollTo(0, 0);
    })

    watch(loader, (value) => {
        if(value === 0) startBgCanvas();
    })

    // onMounted(() => {
    //     startBgCanvas();
    // })
    
    const startBgCanvas = () => {
        const canvas = document.getElementById('bgCanvas')

        const maxWidth = window.innerWidth;
        const maxHeight = window.innerHeight;

        canvas.width = maxWidth;
        canvas.height = maxHeight;

        var gl = canvas.getContext('2d');

        // gl.fillStyle = 'rgb(255, 0, 0, 0.1)';
        // gl.fillRect(100, 100, 100, 100);
        // gl.fillStyle = 'rgb(0, 255, 0, 0.1)';
        // gl.fillRect(400, 100, 100, 100);
        // gl.fillStyle = 'rgb(0, 0, 255, 0.1)';
        // gl.fillRect(300, 200, 100, 100);

        // // line
        // gl.beginPath();
        // gl.moveTo(50, 300);
        // gl.lineTo(300, 100);
        // gl.lineTo(400, 100);
        // gl.strokeStyle = '#fa34a3';
        // gl.stroke();

        // // Arc / Circle
        // gl.beginPath();
        // // gl.arc(x, y, radius, startAngle, endAngle, anticlockwise);
        // gl.arc(100, 100, 50, 0, Math.PI * 2, false);
        // gl.strokeStyle = '#333';
        // gl.stroke();



        // for (let index = 0; index < 100; index++) {
        //     var x = Math.random() * maxWidth;
        //     var y = Math.random() * maxHeight;

        //     gl.beginPath();
        //     gl.arc(x, y, 10, 0, Math.PI * 2, false);
        //     gl.strokeStyle = color;
        //     gl.stroke();
        // }
        // Animation:

        // call function overtime :
        // hm animate c gi  quy lin tc:


        var mouse = {
            x: undefined,
            y: undefined
        }
        var maxRadius = 5;
        var minRadius = 1;
        var colorArray = [
            '#F9EBC8',
            '#FEFBE7',
            '#DAE5D0',
            '#A0BCC2',
        ]
        var initColor = "#ffffff";
        var shadowColor = '#ffffff';
        document.addEventListener('mousemove', (e) => {
            mouse.x = e.x;
            mouse.y = e.y;
        })

        window.addEventListener('resize', () => {
            canvas.width = maxWidth;
            canvas.height = maxHeight;

            init();
        })


        function Circle(x, y, dx, dy, radius, color) {
            this.x = x;
            this.y = y;
            this.dx = dx;
            this.dy = dy;
            this.radius = radius;
            this.minRadius = radius;
            this.color = color;
            this.draw = () => {
                gl.beginPath();
                gl.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false);
                // gl.strokeStyle = this.color;
                // gl.stroke();
                gl.fillStyle = this.color;
                gl.shadowColor = shadowColor;
                // gl.shadowOffsetX = 0;
                // gl.shadowOffsetY = 0;
                // gl.shadowBlur = 50;
                gl.fill();
                gl.closePath();
            }
            this.update = () => {
                if (this.x + this.radius > maxWidth || this.x - this.radius < 0) {
                    this.dx = -this.dx;
                    if(colorArray.includes(this.color)) this.color = initColor;
                }

                if (this.y + this.radius > maxHeight || this.y - this.radius < 0) {
                    this.dy = -this.dy;
                    if(!colorArray.includes(this.color) && this.y + this.radius > maxHeight){
                        this.color = colorArray[Math.floor(Math.random() * colorArray.length)];
                    }
                    else{
                        this.color = initColor;
                    }
                }

                this.x += this.dx;
                this.y += this.dy;

                // interative
                // khong cch t v tr chut ti v tr ca circle < 50
                // if (mouse.x - this.x < 50 && this.x - mouse.x < 50 && mouse.y - this.y < 50 && this.y - mouse.y < 50) {
                //     if (this.radius < maxRadius) { // max kch thc ca circle l maxRadius
                //         this.radius += 1;
                //     }
                // }
                // nu v tr chut ti v tr circle > 50 v radius ca circle  ang trong tnh trng phng to hn
                // mc nh th gim dn v mc nh
                // else if (this.radius > this.minRadius) {
                //     this.radius -= 1;
                // }

                this.draw();
            }
        }

        var circleArray = [];

        function animate() {
            requestAnimationFrame(animate);
            // gl.fillRect(0, 0, maxWidth, maxHeight); // clear ton b ni dung trong khung canvas, clear cc hnh trc .
            gl.clearRect(0, 0, maxWidth, maxHeight); // clear ton b ni dung trong khung canvas, clear cc hnh trc .

            for (let index = 0; index < circleArray.length; index++) {
                circleArray[index].update();
            }
        }

        function init() {
            circleArray = []; // reset
            for (let index = 0; index < 400; index++) {
                var radius = Math.floor(Math.random() * 2 + minRadius); // minRadius and maxRadius default (not maxRadius when moving cursor)
                var x = Math.random() * (maxWidth - radius * 2) + radius;
                var y = Math.random() * (maxHeight - radius * 2) + radius;
                var dx = (Math.random() - 0.001);
                var dy = (Math.random() - 0.001);
                // var color = '#' + (0x1000000 + (Math.random()) * 0xffffff).toString(16).substr(1, 6);
                var color = initColor;
                circleArray.push(new Circle(x, y, dx, dy, radius, color));
            }
        }

        init();
        animate();
    }

    return {
        data
    }
}