<template>
    <div class="wrap-noti">
        <div class="noti-list">
            <div class="noti-item d-flex" v-for="(item, index) in notiList" :key="index">
                <div class="msg">
                    {{item.msg}}
                </div>
                <div class="close-btn d-flex align-items-center" @click="removeNotiHandle(index)">
                    <inline-svg :src="require('@/assets/svg/multiply.svg')" class="icon" />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { notiInit } from './notification';
export default {
    name: "Notification",
    setup() {
        const {
            notiList,
            removeNotiHandle
        } = notiInit();

        return {
            notiList,
            removeNotiHandle
        }
    }
}
</script>

<style lang="scss" scoped src="./notification.scss"></style>