<template>
    <div id="modal-wrapper">
        <div class="dim"></div>
        <div class="modal-content">
            <div class="header d-flex space-between">
                <div class="title">
                    <slot name="header"></slot>
                </div>
                <button v-if="data.hasClose" @click="closeModal">
                    <inline-svg :src="require(`@/assets/svg/multiply.svg`)" class="icon icon-close"></inline-svg>
                </button>
            </div>
            <div class="body">
                <slot name="body"></slot>
            </div>
            <div class="footer">
                <slot name="footer"></slot>
            </div>
        </div>
    </div>
</template>

<script>
import { modalLayoutInit } from './modalLayout';
export default {
    name: 'ModalLayout',
    props: ['hasClose', 'hasDim'],
    setup (props) {
        const {
            data,
            closeModal
        } = modalLayoutInit(props);

        return {
            data,
            closeModal
        }
    }
}
</script>

<style scoped lang="scss" src="./modalLayout.scss"></style>