import { useEmitter } from '@/utils/globalInstance';
import { onMounted, reactive } from 'vue';

export const modalLayoutInit = (props) => {
    const emitter = useEmitter();
    const data = reactive({
        hasClose: true,
        hasDim: true,
    })

    onMounted(() => {
        if(props.hasClose == false) data.hasClose = false;
        if(props.hasDim == false) data.hasDim = false;
    })

    const closeModal = () => {
        emitter.emit('system/closeModal');
    }
    
    return {
        data,
        closeModal
    }
}