import { onMounted, reactive, ref } from "vue";
import companyProjects from '@/utils/companyProjects.json';
import personalProjects from '@/utils/personalProjects.json';
import { useRouter } from 'vue-router';
import { useEmitter } from "@/utils/globalInstance";

export const init = () => {
    const mounted = ref(false);
    const router = useRouter();
    const emitter = useEmitter();

    onMounted(() => {
        setTimeout(() => {
            mounted.value = true;
        }, 0);
    })

    const goDetail = (item) => {
        emitter.emit('system/openMessageBox', { params: { title: 'Alert', msg: "Comming soon!" } });
        // router.push({path: `/projects/${item.id}`})
    }
    
    return {
        mounted,
        companyProjects,
        personalProjects,
        goDetail
    }
} 