import { createRouter, createWebHashHistory } from 'vue-router';
// there is also createWebHistory, createWebHashHistory and createMemoryHistory

import store from '@/store';
import Layout from '@/components/layout/Layout.vue';
import Home from '@/pages/home/Home.vue';
import Profile from '@/pages/profile/Profile.vue';
import PageNotFound from '@/pages/404/PageNotFound.vue';
import Manip from '@/pages/manip/Manip.vue';
import Projects from '@/pages/projects/Projects.vue';
import ProjectDetail from '@/pages/projectDetail/ProjectDetail.vue';
import Sharing from '@/pages/sharing/Sharing.vue';

const router = createRouter({
    history: createWebHashHistory(), // '/base-directory/'
    routes: [
        {
            path: '/',
            name: 'Layout',
            component: Layout,
            children: [
                {
                    path: '',
                    name: 'Home',
                    component: Home
                },
                {
                    path: 'profile',
                    name: 'Profile',
                    component: Profile,
                },
                {
                    path: 'manip',
                    name: 'Manip',
                    component: Manip
                },
                {
                    path: 'projects',
                    name: 'Projects',
                    component: Projects,
                },
                {
                    path: "projects/:id",
                    name: "ProjectDetail",
                    component: ProjectDetail,
                    props: true,
                },
                {
                    path: 'sharing',
                    name: 'Sharing',
                    component: Sharing
                },
            ]
        },
        { path: '/:pathMatch(.*)*', name: 'not-found', component: PageNotFound },
    ],
})

export default router;